<template>
  <div class="wsy_box">
    <div class="wsy_login_container">
      <!-- 头像 -->
      <div class="wsy_avatar_box">
        <img src="@/assets/logo.jpg" alt />
      </div>
      <el-form :model="formData" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
        <el-form-item prop="username">
          <el-input
            type="text"
            prefix-icon="el-icon-user-solid"
            v-model="formData.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            prefix-icon="el-icon-lock"
            v-model="formData.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item class="wsy_btn_box">
          <div class="wsy_btn_content">
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  components: {},
  props: {},
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      inputFlag: "",
      formData: {
        username: "admin",
        password: "123456"
      },
      rules: {
        username: [{ validator: validatePass, trigger: "blur" }],
        password: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  created() {},
  mounted() {},
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios
            .post("/login", {
              username: this.formData.username,
              password: this.formData.password
            })
            .then(res => {
            //   console.log(res);
              if (res.data.meta.status == 400) {
                this.$message.error("登录失败");
                return false;
              } else {
                this.$message({
                  message: "登陆成功",
                  type: "success"
                });
                window.sessionStorage.setItem("token", res.data.data.token);
                this.$router.push("/home");
                window.localStorage.removeItem("defaultRouter")
                window.localStorage.removeItem("str")
                window.localStorage.removeItem("str1")
              }
            });
        } else {
        //   console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy_box {
  width: 100%;
  height: 100%;
  background-color: #2b4b6b;
}
.wsy_login_container {
  background-color: #fff;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 450px;
  height: 300px;
  border-radius: 3px;
  top: 50%;
  > .wsy_avatar_box {
    width: 130px;
    height: 130px;
    padding: 10px;
    border: 1px solid #efefef;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 10px #ddd;
    background-color: #efefef;
    > img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  .el-form {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    > .el-form-item {
      margin-bottom: 22px;
    }
  }
}
.wsy_btn_box {
  display: flex;
  justify-content: flex-end;
}
</style>
